.card{
    justify-content: center;
    align-items: center;
    display: flex;
    height: 40rem;
}


.corpo{
    background: var(--cor-blackTransparent);
    color: var(--cor-branco);
    margin: 10rem 1rem 0 1rem;
    border-radius: 10px;
    width: 73rem;
}

.title{
    justify-content: center;
    align-items: center;
    display: flex;
}

.imgs{
    justify-content: center;
    align-items: center;
    display: flex;
}

.img{
    margin: 0 5px 10px 5px;
    border-radius: 5px;
    width: 33%;
    height: 20rem;
}

.listaTato{
    justify-content: space-evenly;
    align-items: center;
    display: flex;
}

.divImgLista{
    background: var(--cor-blackTransparent);
    margin: 5rem 0.5rem 10rem 0.5rem;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 40%;
    height: 30rem;
}

.imgLista{
    border-radius: 10px;
    height: 95%;
    width: 95%;
}